<template>
	<view :class="wrapCls" :style="wrapStyle">
		<text class="badge-text" :style="textStyle">{{ text }}</text>
	</view>
</template>

<script>
export default {
  
  // text: 
  // inverted: true
  // color: 
  // 1. color(invertedtrue)
  // 2. color(invertedtrue)
  // shape: ellipse()triangle()
  // hasPadding: 10pxpaddingtrue
  props: {
    text: {
      type: [String, Number],
      required: true,
    },
    inverted: {
      type: Boolean,
      default: false,
    },
    color: {
      type: [String, Array],
      default: '#ff0047',
    },
    shape: {
      type: String,
      default: 'ellipse',
    },
    hasPadding: {
      type: Boolean,
      default: true,
    }
  },
  computed: {
    wrapCls() {
      const cls = ['badge-wrap'];
      if (this.hasPadding) {
        cls.push('badge-padding');
      }
      
      if (this.shape === 'triangle') {
        cls.push('badge-triangle');
      }
      else {
        cls.push('badge-ellipse');
      }
      return cls.concat(this.inverted ? [] : ['badge-wrap-border']).join(' ');
    },
    wrapStyle() {
      const 
        color1 = Array.isArray(this.color) ? this.color[0] : this.color,
        color2 = Array.isArray(this.color) ? this.color[1] : 'white',
        styleObj = {};
      if (this.inverted) {
        styleObj.background = color1;
      }
      else {
        styleObj.background = color2;
        styleObj.borderColor = color1;
      }

      return styleObj;
    },
    textStyle() {
      const 
        color1 = Array.isArray(this.color) ? this.color[0] : this.color,
        color2 = Array.isArray(this.color) ? this.color[1] : 'white';
      return {
        color: this.inverted ? color2 : color1,
      };
    },
  },
}
</script>

<style lang="scss" scoped>
.badge-wrap {
  display: flex;
  min-width: 32upx;
  height: 32upx;
  justify-content: center;
  align-items: center;
}
.badge-padding {
  padding: 0 8upx;
}
.badge-ellipse {
  border-radius: 32upx;
}
.badge-triangle {
  border-radius: 8upx;
  border-bottom-right-radius: 32upx;
}
.badge-wrap-border {
  border-style: solid;
  border-width: 0.5upx;
}
.badge-text {
  font-size: 18upx;
  line-height: 18upx;
}
</style>
